body{
    background-color: #292929 !important;
    padding: 0px;
    margin: 0px;
}


.App{
    padding: 96px 0px 56px 0px;
    color: #ffffff;
    height: 1458px;
    max-width: 1440px;
    margin: 0 auto;
}





*{
    font-family: 'IBM Plex Mono' !important;
}

.nav__content {
    display: grid;
    grid-template-columns: minmax(-webkit-min-content, 576px) minmax(-webkit-min-content, 656px);
    grid-template-columns: minmax(min-content, 576px) minmax(min-content, 656px);
    justify-content: space-between;	
    margin: 0 auto;
    
}

@media screen and (max-width: 834px){
    .nav__content{
        grid-template-columns: minmax(-webkit-min-content, 738px);
        grid-template-columns: minmax(min-content, 738px);
        grid-template-rows: 400px auto;
        grid-gap: 48px 0px;
        padding: 48px 48px;
    }
}

@media screen and (max-width: 564px){
    .nav__content{
        grid-gap: 160px 0px;

    }
}

@media screen and (max-width: 564px){
    .nav__content{
        grid-gap: 180px 0px;
    }

}
h1, h2, h3, h4{
  -webkit-margin-after: unset;
          margin-block-end: unset;
  -webkit-margin-before: unset;
          margin-block-start: unset;
}

.grid__container{
    display: grid;
    grid-gap: 16px 0px ;
    overflow-y: auto;
    overflow-x: hidden;
    height: 728px;
}

.grid__container > a:nth-child(n){
  background: #5E5E5E;
  text-align: center;
  text-decoration: none;
  padding: 32px 0px 0px 48px;
  text-align: left;
  height: 200px;

}

#root > div > div > div:nth-child(2) > section.grid__container > a:nth-child(n) > h3{
  color: #ffffff;
  font-family: IBM Plex Sans !important;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px !important;
  text-transform: uppercase;
}

#root > div > div > div:nth-child(2) > section.grid__container > a:nth-child(n) > h2{
  color: #ffffff;
  font-family: IBM Plex Mono;
  font-style: normal;
  font-weight: bold;
  font-size: 32px;
  line-height: 32px;
  padding: 24px 0px 8px 0px;
}


#root > div > div > div:nth-child(2) > section.grid__container > a:nth-child(n) > h4{
  color: #ffffff;
  font-family: IBM Plex Sans !important;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px !important;
  padding: 0px 0px 32px 0px;
}

#root > div > div > div:nth-child(2) > section.grid__container > a:nth-child(n) > span{
  color: #ffffff;
  font-family: IBM Plex Sans !important;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px !important;
}

#root > div > div > div:nth-child(2){
  display: grid;
  grid-gap: 144px 0px;
}

#root > div > div > div:nth-child(2) > section:nth-child(2){
  display: grid;
  grid-gap: 32px 0px;
}



.grid__container::-webkit-scrollbar {
  width: 5px;
}
.grid__container::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 0px transparent;
}
.grid__container::-webkit-scrollbar-thumb {
background-color: #ffffff;
}
.left__content{
    display: grid;
    height: auto;
    grid-gap: 38px 0px;
    padding: 0px 48px;

}

h1{
    font-size: 80px;
    font-weight: bold;
    line-height: 104px;
    text-align: left;
    vertical-align: top;
    margin-top: 0px;
}

p{
    font-size: 16px;
    font-style: regular;
    line-height: 24px;
    text-align: left;
    vertical-align: top;
}


.social{
    display: flex;
    align-items: center;
}

#root > div > div > div.left__content > section{
    display: grid;
    grid-gap: 601px 0px;
    margin-top: 176px;
}

#root > div > div > div.left__content > section > nav > section > a:nth-child(n){
    color: #B2B2B2;
    display: block;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    word-spacing: 52px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-transform: uppercase;
    text-decoration: none;
}


#root > div > div > div.left__content > section > section.social > img{
    width: 48px;
    height: 48px;
    background: grey;
    border-radius: 25px;
}

#root > div > div > div.left__content > section  > section.social > a:nth-child(n){
    margin-left: 32px;
    color: #FFFFFF;
    text-transform: uppercase;
    text-decoration: none;
}

#root > div > div > div.left__content > section > nav > div{
    width: 20px;
    height: 4px;
    border-top: 2px solid white;
    border-bottom: 2px solid white;
}

@media screen and (max-width: 834px){
    .left__content{
        display: grid;
        grid-template-columns: minmax(-webkit-min-content, 576px);
        grid-template-columns: minmax(min-content, 576px);
        padding: 0px;
    }
    
    #root > div > div > div.left__content > section > nav{
        top: 18px;
        left: 12px;
        position: fixed;
    }


    #root > div > div > div.left__content > section > nav > section{
        display: none;
    }

    #root > div > div > div.left__content > section > section{
        display: none;
    }

    /* HAMBURGER */
    #myLinks{
            display: none;
    }
    #root > div > div > div.left__content > section > nav > section > a:nth-child(n){
        float: left;
        color: white;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 14px;
        word-spacing: 16px;
    }

    #root > div > div > div.left__content > section > nav > div{
        display: inline;
        display: initial;
        top: 40px;
        right: 10px;
        position: fixed;    
        -webkit-transition: all 400ms cubic-bezier(.47,1.64,.41,.8);    
        transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
    }

}

@media screen and (max-width: 785px){
    #root > div > div > div.left__content > h1{
        font-size: 60px;
        line-height: 84px;

    }
}

@media screen and (max-width: 688px){
    #root > div > div > div.left__content > h1{
        font-family: IBM Plex Mono;
        font-style: normal;
        font-weight: bold;
        font-size: 48px;
        line-height: 62px;
    }

}
