.left__content{
    display: grid;
    height: auto;
    grid-gap: 38px 0px;
    padding: 0px 48px;

}

h1{
    font-size: 80px;
    font-weight: bold;
    line-height: 104px;
    text-align: left;
    vertical-align: top;
    margin-top: 0px;
}

p{
    font-size: 16px;
    font-style: regular;
    line-height: 24px;
    text-align: left;
    vertical-align: top;
}


.social{
    display: flex;
    align-items: center;
}

#root > div > div > div.left__content > section{
    display: grid;
    grid-gap: 601px 0px;
    margin-top: 176px;
}

#root > div > div > div.left__content > section > nav > section > a:nth-child(n){
    color: #B2B2B2;
    display: block;
    font-weight: bold;
    font-size: 16px;
    line-height: 24px;
    word-spacing: 52px;
    width: fit-content;
    text-transform: uppercase;
    text-decoration: none;
}


#root > div > div > div.left__content > section > section.social > img{
    width: 48px;
    height: 48px;
    background: grey;
    border-radius: 25px;
}

#root > div > div > div.left__content > section  > section.social > a:nth-child(n){
    margin-left: 32px;
    color: #FFFFFF;
    text-transform: uppercase;
    text-decoration: none;
}

#root > div > div > div.left__content > section > nav > div{
    width: 20px;
    height: 4px;
    border-top: 2px solid white;
    border-bottom: 2px solid white;
}

@media screen and (max-width: 834px){
    .left__content{
        display: grid;
        grid-template-columns: minmax(min-content, 576px);
        padding: 0px;
    }
    
    #root > div > div > div.left__content > section > nav{
        top: 18px;
        left: 12px;
        position: fixed;
    }


    #root > div > div > div.left__content > section > nav > section{
        display: none;
    }

    #root > div > div > div.left__content > section > section{
        display: none;
    }

    /* HAMBURGER */
    #myLinks{
            display: none;
    }
    #root > div > div > div.left__content > section > nav > section > a:nth-child(n){
        float: left;
        color: white;
        padding: 14px 16px;
        text-decoration: none;
        font-size: 14px;
        word-spacing: 16px;
    }

    #root > div > div > div.left__content > section > nav > div{
        display: initial;
        top: 40px;
        right: 10px;
        position: fixed;    
        transition: all 400ms cubic-bezier(.47,1.64,.41,.8);
    }

}

@media screen and (max-width: 785px){
    #root > div > div > div.left__content > h1{
        font-size: 60px;
        line-height: 84px;

    }
}

@media screen and (max-width: 688px){
    #root > div > div > div.left__content > h1{
        font-family: IBM Plex Mono;
        font-style: normal;
        font-weight: bold;
        font-size: 48px;
        line-height: 62px;
    }

}